.App {
  text-align: center;

  margin: 0 auto;
  padding: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.button {
  position: relative;
  background-color: darkgoldenrod;
  font-size: 18px;
  border: 0;
  cursor: pointer;
  margin: 10px;
  padding: 5px 20px;
  transition: opacity 0.3s linear;
  border-radius: 5px;
}

.button:active {
  top: 2px;
}

.button:disabled {
  opacity: 0.5;
  cursor: auto;
  top: 0;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
